import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    localAlertShow: false, //alert
    waittingShow:false, //waitting
    routerParama:"wallet" //
  },
  mutations: {
    // alert
    localAlertOn (state) {
      state.localAlertShow = true;
    },

    // alert
    localAlertOff (state) {
      state.localAlertShow = false;
    },

    // waitting
    waittingOn(state){
      state.waittingShow = true;
    },

    // waiting
    waittingOff(state){
      state.waittingShow = false;
    },
    //
    changeRouterParama(state,payload){
      state.routerParama = payload.text;
    }
  }
});

export default store



