export default {
  name: "addLog",
  data(){
    return{
      status:"", //
      expensiveIndex:-1, //
      incomeIndex:-1, //
      keyValue:"0", //
      remarks:"", //
      reload:false, //
      expensiveImgUrl:'', //
      incomeImgUrl:'', //
      // 
      expensesList:[
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        },
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        },
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        },
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        },
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        },
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        },
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        }
      ],
      // 
      incomeList:[
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        },
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        },
        {
          imgUrl:require("@/assets/img/example.svg"),
          title:""
        }
      ]
    }
  },
  computed:{
    // 
    textAcive:function () {
      return(this.status === "");
    },
    clBtnShow:function () {
      return(!(this.keyValue === '0'));
    }
  },
  methods:{
    // 
    changeStatus:function (status) {
      this.status = status;
    },
    // 
    goBack:function () {
      // 
      this.$router.push('/mainPage/' + this.$store.state.routerParama);
    },

    // 
    chooseCategory:function (index,imgUrl) {
      if(this.status === ''){
        this.expensiveIndex = index;
        this.expensiveImgUrl = imgUrl;
      } else if (this.status === ''){
        this.incomeIndex = index;
        this.incomeImgUrl = imgUrl;
      }
      this.imgUrl = imgUrl;
      console.log(index);
    },

    // 
    clickKey:function (keyValue) {
      this.keyValue += keyValue;
      // 00
      if(this.keyValue.length === 2 && this.keyValue.slice(0,1) === '0' && !(keyValue === '.')){
          this.keyValue = this.keyValue.slice(1)
      }
      // 
      if(this.keyValue.indexOf('.') > -1){
        // 
        if(this.keyValue.split('.').length > 2){
          this.backKey();
        }
        // 
        if(this.keyValue.split('.')[1].length > 2){
          this.backKey();
        }
      }
      // 
      if(this.keyValue.length > 12){
        this.backKey();
      }
      console.log(this.keyValue);
    },

    // 
    backKey:function () {
      this.keyValue = this.keyValue.slice(0,-1);
      if(this.keyValue.length < 1){
        this.keyValue = '0';
      }
    },

    // 
    clearAll:function () {
      this.keyValue = '0';
    },

    // 
    goRemarks:function () {
      // 
      if(this.expensiveIndex === -1 && this.incomeIndex === -1){
        this.$alert.on('');
        return;
      }
      if(this.remarks === ''){
        this.remarks = '';
      }
      this.$router.push({
        name:"addLogRemarks",
        params:{
          remarks:this.remarks, //
          status:this.status, //or
          expensiveIndex:this.expensiveIndex, //index
          incomeIndex:this.incomeIndex, //index
          expensesList:this.expensesList, //
          incomeList:this.incomeList, //
          expensiveImgUrl:this.expensiveImgUrl, //
          incomeImgUrl:this.incomeImgUrl, //
          money:this.keyValue //
        }
      });
    },

    // 
    makeSure:function () {
      console.log("makeSure");
      // 
      if(this.expensiveIndex === -1 && this.incomeIndex === -1){
        this.$alert.on('');
        return;
      }
      // 
      let self = this;
      let categoryName = "";
      let imgUrl = "";
      if(this.status === ''){
        categoryName = self.expensesList[self.expensiveIndex].title;
        imgUrl = self.expensesList[self.expensiveIndex].imgUrl;
      } else if(this.status === ''){
        categoryName = self.incomeList[self.incomeIndex].title;
        imgUrl = self.incomeList[self.incomeIndex].imgUrl;
      }
      let remarks = '';
      if(self.remarks === ''){
        remarks = '';
      } else {
        remarks = self.remarks;
      }
      this.$cAxios.post(self.$constantIP.submitLog, {
        params:{
          category:self.status, //or
          categoryName:categoryName, //
          money:self.keyValue, //
          remarks:remarks, //
          imgUrl:imgUrl //
        }
      })
      .then(function (response) {
        self.$waitting.off();
        if(response.data.returnCode === '000000'){
          self.$alert.on('',() => {
            status:"", //
              self.expensiveIndex = -1; //
              self.incomeIndex = -1; //
              self.keyValue = "0"; //
              self.remarks = ""; //
              self.reload = false; //
              self.expensiveImgUrl = ''; //
              self.incomeImgUrl = ''; //
          });
        }
      })
    }
  },
  mounted:function () {
    // 
    if(this.$route.params.remarks){
      this.remarks = this.$route.params.remarks;
    }
  },
  // 
  beforeRouteEnter:function(to, from, next) {
    // console.log(to);
    // console.log(from);
    // console.log(next);
    next(vm => {
      if(from.name === "addLogRemarks"){
        vm.reload = false;
        vm.status = vm.$route.params.status; //or
        vm.expensiveIndex = vm.$route.params.expensiveIndex; //index
        vm.incomeIndex = vm.$route.params.incomeIndex; //index
        vm.expensiveImgUrl = vm.$route.params.expensiveImgUrl; //
        vm.incomeImgUrl = vm.$route.params.incomeImgUrl; //
        vm.expensesList = vm.$route.params.expensesList;//
        vm.incomeList = vm.$route.params.incomeList; //
        vm.keyValue = vm.$route.params.money; //
        console.log("");
      } else {
        vm.reload = true;
        console.log("");
      }
    });

  }
}



