export default {
  name: "addLogRemarks",
  data(){
    return {
      remarks:"", //
      index:0, //index
      list:[], //
      status:"", //or
      expensiveIndex:-1, //index
      incomeIndex:-1, //index
      expensesList:[], //
      incomeList:[], //
      expensiveImgUrl:'', //
      incomeImgUrl:'', //
      money:'' //
    }
  },
  methods:{
    // 
    goBack:function () {
      this.$router.push({
        name:"addLog",
        params:{
          status:this.status, //or
          expensiveIndex:this.expensiveIndex, //index
          incomeIndex:this.incomeIndex, //index
          expensiveImgUrl:this.expensiveImgUrl, //
          incomeImgUrl:this.incomeImgUrl, //
          expensesList:this.expensesList, //
          incomeList:this.incomeList, //
          money:this.money //
        }
      });
    },
    // 
    goComplete:function () {
      this.$router.push({
        name:"addLog",
        params:{
          remarks:this.remarks,
          status:this.status, //or
          expensiveIndex:this.expensiveIndex, //index
          incomeIndex:this.incomeIndex, //index
          expensesList:this.expensesList, //
          incomeList:this.incomeList, //
          expensiveImgUrl:this.expensiveImgUrl, //
          incomeImgUrl:this.incomeImgUrl, //
          money:this.money //
        }
      });
    }
  },
  beforeMount:function(){
    // 
    if(this.$route.params.status === ''){
      this.index = this.$route.params.expensiveIndex;
      this.list = this.$route.params.expensesList;
    } else if(this.$route.params.status === ''){
      this.index = this.$route.params.incomeIndex;
      this.list = this.$route.params.incomeList;
    }
    this.remarks = this.$route.params.remarks;
    this.status = this.$route.params.status; //or
    this.expensiveIndex = this.$route.params.expensiveIndex; //index
    this.incomeIndex = this.$route.params.incomeIndex; //index
    this.expensiveImgUrl = this.$route.params.expensiveImgUrl; //
    this.incomeImgUrl = this.$route.params.incomeImgUrl; //
    this.expensesList = this.$route.params.expensesList;//
    this.incomeList = this.$route.params.incomeList; //
    this.money = this.$route.params.money; //
  },
  mounted:function () {

  }
}



