import store from '../store/vuexIndex.js'
  export default {
    name: "localAlert",
    data() {
      return{
        // ifShow:false,  //
        message:null,  //
        callback:null  //
      }
    },
    computed:{
      ifShow:{
        get:function () {
          return store.state.localAlertShow;
        },
        set:function (newValue) {
          store.state.localAlertShow = newValue;
        }
      }
    },
    methods:{
      // 
      off:function () {
        this.ifShow = false;
        if(typeof this.callback === "function"){
          this.callback();
        }
        this.callback = null;
      }
    }
  }



